import React, { useState } from "react";
import axios from "axios";
import './Contact.css';
import './App.css';

function Contact() {
    const [serverState, setServerState] = useState({
        submitting: false,
        status: null
    });
    const handleServerResponse = (ok, msg, form) => {
        setServerState({
            submitting: false,
            status: { ok, msg }
        });
        if (ok) {
            form.reset();
        }
    };
    const handleOnSubmit = e => {
        e.preventDefault();
        const form = e.target;
        setServerState({ submitting: true });
        axios({
            method: "post",
            url: "https://formspree.io/xyynbnyr",
            data: new FormData(form)
        })
            .then(r => {
                handleServerResponse(true, "Thanks!", form);
            })
            .catch(r => {
                handleServerResponse(false, r.response.data.error, form);
            });
    };
    return (
        <div>
            <div id="Contact">
                <h2>Contact Me</h2>
                <div className="Line"><hr></hr></div>
                <div className="contact-explanation">
                    <div className="form">
                        <p className="form-intro">Thank you for taking the time to look through my works and portfolio, if you'd like to talk more please fill out your name and a brief message</p>
                        <div className="form-div">
                            <form className="actual-form" onSubmit={handleOnSubmit}>
                                <label htmlFor="email" className="labels">Email:</label>
                                <input id="email" type="email" name="email" className="input" required />

                                <label htmlFor="message" className="labels">Message:</label>
                                <textarea id="message" name="message" className="input"></textarea>

                                <button className="Contact-Button" type="submit" disabled={serverState.submitting}>
                                    Submit
        </button>
                                {serverState.status && (
                                    <p className={!serverState.status.ok ? "errorMsg" : ""}>
                                        {serverState.status.msg}
                                    </p>
                                )}
                            </form>
                        </div>
                    </div>
                </div>

            </div>
            <div className="banner">
                <div className="quote">
                    <p>"The true method of knowledge is experiment"</p>
                    <p>-William Blake</p>
                </div>
            </div>
        </div>
    );
};

export default Contact;